import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'Index',
      component: ()=>import('../components/index')
    },
    {
      path: '/404',
      name: '404',
      component: ()=>import('../components/404')
    },
    {
      path:'/login',
      name:'login',
      component: ()=>import('../components/login')
    },
    {
      path: '/reset-pwd',
      name:'resetPassword',
      component: ()=>import('../components/reset-pwd')
    },
    {
      path:'/pcr/pool-setting',
      name:'pcr-pool-setting',
      meta:{auth:true},
      component: ()=>import('../components/pool-setting')
    },
    {
      path:'/plugins-dict',
      name:'plugins-dict',
      component: ()=>import('../components/plugins-dict')
    },
    {
      path: '*',
      redirect: '/404'
    }
  ]
})



