// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import {router} from './router'
import ElementUi from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import {ajax,addInterceptors} from './api'
import VueCookies from 'vue-cookies'

Vue.config.productionTip = false
Vue.use(ElementUi)
Vue.use(VueCookies)

Vue.prototype.$ajax = ajax
Vue.prototype.checkAuth = checkAuth

/* eslint-disable no-new */
const vue = new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

addInterceptors(vue)

router.beforeEach((to,from,next) => {
  if(to.matched.some( m => m.meta&&m.meta.auth )) {
    checkAuth().then(next()).catch(err=>next(err))
  } else {
    next()
  }
})

function checkAuth() {
  return new Promise((resolve, reject) => {
    let token = vue.$cookies.get('token')
    let salt = vue.$cookies.get('salt')
    if(token&&salt){
      vue.$ajax({
        url:'/token.valid',
        params:{token:token,salt:salt}
      }).then(res=>{
        if (res['code']===0){
          resolve()
        }else {
          vue.$message({
            type:'error',
            message:res['msg'],
            duration:1000,
            onClose:()=>{
              reject('/login')
            }
          })
        }
      }).catch(e=>{
        reject('/index')
      })
    }else{
      vue.$message({
        type:'error',
        message:'未登录',
        duration:1000,
        onClose:()=>{
          reject('/login')
        }
      })
    }
  })
}



